  display: block;\r\n        width: 40%;\r\n        border-radius: 15px;\r\n        border: none;\r\n        padding: 10px 0px;\r\n        margin: 40px auto;\r\n        background-color: var(--green);\r\n        color: #fff;\r\n        box-shadow: none;\r\n    }\r\n\r\n    .feature-add {\r\n        display: block;\r\n        cursor: pointer;\r\n        width: 50px;\r\n        height: 50px;\r\n        margin: 20px auto;\r\n        border: none;\r\n        background-color: #fff;\r\n        background-image: url('./../img/plus.svg');\r\n        background-position: center;\r\n        background-repeat: no-repeat;\r\n    }\r\n\r\n\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

