exports.push([module.id, "\n:root{font-family:Open Sans,sans-serif\n}\n.sale{width:calc(4.16667vw * 19);padding-left:4.16667vw;padding-right:4.16667vw;display:flex;flex-flow:column;justify-content:center;align-items:center;min-height:100vh\n}\n.sale-controls{position:relative;width:100%\n}\n.sale-controls__load-btn{position:absolute;left:35%;top:0;width:30%;height:calc(8.33333vh - 40px);padding:10px;border-radius:15px;text-align:center;background-color:#008f9f;color:#fff\n}\n.sale-controls__file{display:none\n}\n.sale-progressbar{position:absolute;left:0;top:0;width:100%;border-radius:15px;background-color:#fff;height:calc(8.33333vh - 40px);overflow:hidden\n}\n.sale-progressbar__bg,.sale-progressbar__progress{font-weight:700;text-align:center;position:absolute;top:0;left:0;width:0;height:100%\n}\n.sale-progressbar__bg{width:100%;z-index:102\n}\n.sale-progressbar__progress{z-index:101;background-color:#00a287;transition:all .2s ease-in-out\n}\n.sale-controls-link{position:absolute;left:0;top:0;width:100%;height:calc(8.33333vh - 40px);text-align:center;color:#008f9f\n}", "", {"version":3,"sources":["D:/nodejs/learning_node/nnz-scrapping/src/css/variables.css","D:/nodejs/learning_node/nnz-scrapping/src/vue-comp/src/vue-comp/sales.vue"],"names":[],"mappings":";AAEA,MAQI,gCAAqC;CACxC;AC+ED,MACA,2BAAA,uBACA,wBACA,aACA,iBACA,uBACA,mBACA,gBACA;CACA;AAEA,eACA,kBAAA,UACA;CACA;AAEA,yBACA,kBAAA,SACA,MACA,UACA,8BACA,aACA,mBACA,kBACA,yBACA,UACA;CACA;AAEA,qBACA,YAAA;CACA;AAEA,kBACA,kBAAA,OACA,MACA,WACA,mBACA,sBACA,8BACA,eACA;CACA;AAEA,kDAEA,gBAAA,kBACA,kBACA,MACA,OACA,QACA,WACA;CAEA;AAEA,sBACA,WAAA,WACA;CACA;AAEA,4BACA,YAAA,yBACA,8BAGA;CACA;AAEA,oBACA,kBAAA,OACA,MACA,WACA,8BACA,kBACA,aACA;CACA","file":"sales.vue","sourcesContent":["@import \"http://fonts.fontstorage.com/import/opensans.css\";\r\n\r\n:root {\r\n    --marine: #008F9F;\r\n    --green: #00A287;\r\n    --light-grey: #f6f6f9;\r\n    --column: calc(100vw / 24);\r\n    --row: calc(100vh / 12);\r\n    --column-mobile: calc(100vw / 12);\r\n    --row-mobile: calc(100vh / 24);\r\n    font-family: 'Open Sans', sans-serif;\r\n}\r\n","<template lang=\"html\">\r\n\r\n    <div class=\"sale\">\r\n        <p class=\"sale-info\"></p>\r\n\r\n        <div class=\"sale-controls\">\r\n            <label v-show=\"stage === 1\" class=\"sale-controls__load-btn\" for=\"tablefile\"> </label>\r\n            <input class=\"sale-controls__file\" type=\"file\" name=\"tablefile\" id=\"tablefile\" @change=\"loadFileHandler\">\r\n            <div v-show=\"stage === 2\" class=\"sale-progressbar\">\r\n                <p class=\"sale-progressbar__bg\">{{ Math.round(progress.current / progress.total * 100) }}%</p>\r\n                <div :style=\"{width: progress.current / progress.total * 100 +'%'}\" class=\"sale-progressbar__progress\"></div>\r\n            </div>\r\n            <div v-show=\"stage === 3\" class=\"sale-controls-link\">\r\n                <a :href=\"link\">{{link}}</a>\r\n            </div>\r\n        </div>\r\n    </div>\r\n\r\n</template>\r\n\r\n<script>\r\n\r\nimport axios from './../../node_modules/axios/dist/axios.js';\r\n\r\nexport default {\r\n\r\n    data() {\r\n        return {\r\n            stage: 1,\r\n            link: '',\r\n            progress: {\r\n                total: 1,\r\n                current: 1\r\n            }\r\n        }\r\n    },\r\n\r\n    methods: {\r\n        // \r\n        loadFileHandler(e) {\r\n            // TODO:    \r\n            let vm = this;\r\n            let file = e.target.files[0];\r\n            if(file) {\r\n                const config = { headers: { 'Content-Type': 'multipart/form-data' } };\r\n                let formData = new FormData();\r\n                formData.append('tablefile', file, file.name);\r\n                this.stage = 2;\r\n                axios.post('/getsales', formData, config)\r\n                .then( (res) => {\r\n\r\n                    let barId = res.data.bar;\r\n                    console.log(barId);\r\n                    let intervalId = setInterval( () => {\r\n                        axios.get(`/salesprogress?id=${barId}`)\r\n                        .then( (res) => {\r\n                            if(res.data.current === res.data.total) {\r\n                                vm.progress.current = res.data.current;\r\n                                vm.link = res.data.link;\r\n                                vm.stage = 3;\r\n                                console.log(res.data.link);\r\n                                clearInterval(intervalId);\r\n\r\n                            }\r\n                            else {\r\n                                vm.progress.current = res.data.current;\r\n                                vm.progress.total = res.data.total;\r\n                            }\r\n\r\n                        })\r\n                        .catch( (err) => {\r\n                            console.log(err);\r\n                        })\r\n                    }, 1000)\r\n\r\n                })\r\n                .catch( (err) => {\r\n                    console.log(err)\r\n                });\r\n            }\r\n        }\r\n    }\r\n\r\n}\r\n</script>\r\n\r\n<style lang=\"css\">\r\n\r\n@import './../css/variables.css';\r\n\r\n.sale {\r\n    width: calc(var(--column)  * 19);\r\n    padding-left: var(--column);\r\n    padding-right: var(--column);\r\n    display: flex;\r\n    flex-flow: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    min-height: 100vh;\r\n}\r\n\r\n.sale-controls {\r\n    position: relative;\r\n    width: 100%;\r\n}\r\n\r\n.sale-controls__load-btn {\r\n    position: absolute;\r\n    left: 35%;\r\n    top: 0%;\r\n    width: 30%;\r\n    height: calc(var(--row) - 40px);\r\n    padding: 10px;\r\n    border-radius: 15px;\r\n    text-align: center;\r\n    background-color: var(--marine);\r\n    color: #fff;\r\n}\r\n\r\n.sale-controls__file {\r\n    display: none;\r\n}\r\n\r\n.sale-progressbar {\r\n    position: absolute;\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%;\r\n    border-radius: 15px;\r\n    background-color: #fff;\r\n    height: calc(var(--row) - 40px);\r\n    overflow: hidden;\r\n}\r\n\r\n.sale-progressbar__bg,\r\n.sale-progressbar__progress {\r\n    font-weight: bold;\r\n    text-align: center;\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 0;\r\n    height: 100%;\r\n\r\n}\r\n\r\n.sale-progressbar__bg {\r\n    width: 100%;\r\n    z-index: 102;\r\n}\r\n\r\n.sale-progressbar__progress {\r\n    z-index: 101;\r\n    background-color: var(--green);\r\n    -webkit-transition: all .2s ease-in-out;\r\n    -o-transition: all .2s ease-in-out;\r\n    transition: all .2s ease-in-out;\r\n}\r\n\r\n.sale-controls-link {\r\n    position: absolute;\r\n    left: 0;\r\n    top: 0;\r\n    width: 100%;\r\n    height: calc(var(--row) - 40px);\r\n    text-align: center;\r\n    color: var(--marine);\r\n}\r\n\r\n\r\n\r\n</style>\r\n"],"sourceRoot":""}]);

